.friend {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  transition-duration: 0.5s;
  user-select: none;
}

.friend:hover {
  background-color: rgb(0, 0, 0, 0.05);
  border-radius: 25px;
}

.friend .friend__avatar {
  width: 50px;
  border-radius: 38%;
}

.friend__avatar.g-avatar {
  width: 70px;
}

.friend__avatar.m-avatar {
  width: 60px;
}

.friend .friend__column {
  display: flex;
  align-items: center;
}

.friend .friend__avatar {
  margin-right: 15px;
  user-select: none;
}

.friend__content .friend__name {
  display: block;
  margin-bottom: 5px;
}

.friend__content .friend__status,
.friend__content .friend__bottom-text {
  opacity: 0.6;
}

.friend__content .friend__bottom-text {
  font-size: 14px;
}

.friend__content .friend__status,
.friend .chat__timestamp {
  font-size: 12px;
}

.friend .friend__now-listening {
  border: 2px solid #5aad77;
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 14px;
  user-select: none;
}

.friend .friend__now-listening span {
  opacity: 0.6;
}

.friend .friend__now-listening i {
  font-size: 12px;
}

.friend .friend__add-btn {
  display: flex;
  padding: 10px 15px;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
  border-radius: 5px;
  cursor: pointer;
}

.friend .friend__add-btn:hover {
  background-color: #cccccc;
}
