.find {
  margin-top: 50px;
}

.find .find__header-list {
  display: flex;
  justify-content: space-around;
}

.find .header-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  user-select: none;
}

.find .header-btn:hover {
  opacity: 0.8;
}

.find .header-btn__title {
  display: block;
  margin-top: 20px;
  font-size: 14px;
}

.find .header-btn__icon {
  font-size: 12px;
}

.find .find__recommended {
  margin-top: 20px;
  padding-top: 15px;
  border-top: 1px solid #f7f7f7;
}

.find .find__title {
  font-size: 12px;
  opacity: 0.7;
  margin-bottom: 20px;
  user-select: none;
}
